    MDBContainer,
    MDBRow,
    MDBCol,
    MDBCard,
    MDBCardBody,
    MDBCardTitle,
    MDBCardText,
    MDBCardImg,
    MDBBtn,
    MDBCardGroup,
    MDBAccordion,
    MDBAccordionItem,
  },
  methods: {
        scrollToAnchorPoint(refName) {
            const el = this.$refs[refName]
            if (el) {
                el.scrollIntoView({ behavior: 'auto', block: 'start' })
            }
            else {
                console.error(`No element found with ref "${refName}"`)
            }
        },
  }
}
